<template>
  <div id="home"> 
    <Banner></Banner>
    
    <el-row class="wrap">
       <el-col :xs="{span:22,offset:1}" :sm="{span:20,offset:2}" class="left">
        <div class="left">
         
					<transition enter-active-class="animated zoomIn">
						<h2 v-if="show">技术栈</h2>
					</transition>
					
					<div class="content">
								<ul class="showContent">
										<li>1、<a href="https://cn.vuejs.org/" target="_blank">C++</a></li>
										<li>2、<a href="https://cn.vuejs.org/" target="_blank">QT</a></li>
										<li>3、<a href="https://cn.vuejs.org/" target="_blank">python</a></li>
										<li>4、<a href="https://cn.vuejs.org/" target="_blank">vue</a></li>
								</ul>

								<p> 首页test</p>
							</div>
				</div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
	import Banner from '@/components/banner'

	export default {
		data() {
			return {
				show: false,
			}
		},
		mounted() {
			this.show = true
		},
		computed: {
			isPhone() {
				return window.screen.width < 520
			}
		},
		components: {
			Banner,
		}
	}

</script>

<style lang="less" scoped>
	#home {
		margin-top: 0;
	}
	
	.wrap {
		margin-top: 20px;
	}
	
	.animateTran(@X, @Y) {
		-moz-transform: translate( @X, @Y );
		-webkit-transform: translate( @X, @Y );
		-ms-transform: translate( @X, @Y );
		-o-transform: translate( @X, @Y );
		transform: translate( @X, @Y );
	}
	
	.left {
		h2 {
			font-size: 0.35rem;
			margin-bottom: 20px;
			display: block;
			border-bottom: 1px solid;
		}
		.showContent,
		p {
			text-indent: 2em;
			margin-bottom: 20px;
			font-size: 0.2rem;
			line-height: 1.7em;
		}
		.showContent {
			color: #fff;
			background-color: #272822;
			border-radius: 5px;
			text-indent: 0;
			padding: 1em;
			padding-left: 2em;
			a {
				color: #fff;
			}
		}
	}

</style>



